.al-tabs {
  width: 100%;
  .MuiTabs-root{
    min-height: 40px;
  }
  .MuiTab-root {
    border: 0.5px solid #d0d0d0;
    background-color: white;
    border-radius: 0 15px 0 0;
    z-index: 5;
    width: 200px;
    min-height: 40px;
    padding: 0;
    font-size: 12px;
    text-transform: capitalize;
    color: black;
    &.Mui-selected {
      border-bottom: none;
      color: #4578C8;
      font-weight: 600;
    }
  }
  .al-tabs-panel{
    padding: 20px;
    position: relative;
    top: -1px;
    border: 0.5px solid #d0d0d0;
    z-index: 2;
    background-color: white;
  }
}
