.employer-details {
  a {
    text-decoration: none;
  }
  .al-details {
    .sub-header {
      font-size: 14px;
      font-weight: 600;
      margin-top: 20px;
    }
    .al-input {
      width: 33%;
      margin-top: 20px;
      .form-label {
        margin: 5px 0;
        font-size: 12px;
      }
      .MuiOutlinedInput-root {
        border-radius: 10px;
        height: 45px;
      }
    }
    .action-btn {
      margin-top: 20px;
      .MuiButton-root {
        margin-right: 15px;
      }
    }
    .add-branch,
    .add-invoice-location,
    .add-users {
      margin: 0.5%;
      width: 98%;
    }
  }
  .job-list-filters {
    width: 100%;
    margin-top: 15px;
    background-color: white;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 1px 4px 0 rgb(0 0 0 / 20%);
    .job-list-filters-header {
      font-weight: 600;
    }
  }
  .job-list {
    width: 100%;
    margin-top: 10px;
  }
}
