@use '../../../styling/layout';

.candidate-details {
  @extend .d-flex;
  background-color: white;
  margin: 15px 0;
  padding: 15px 25px;
  border-radius: 5px;
  box-shadow: 0 1px 4px 0 rgb(0 0 0 / 10%);
  font-size: 12px;
  color: #535353;
  .al-select {
    .MuiOutlinedInput-root {
      height: 30px;
      color: #4578c8;
      font-size: 12px;
      font-weight: 600;
    }
    .MuiOutlinedInput-notchedOutline {
      border-color: #ededed;
      max-width: 110px;
      padding: 5px 10px;
      border-radius: 5px;
      height: 25px;

      border: 1px solid #e6e6e6;
      color: #4578c8;
      font-size: 12px;
    }
  }
  .candidate-header {
    .candidate-status {
      .al-select {
        width: 10%;
      }
    }
  }
  .candidates-action {
    .MuiButton-root {
      margin-right: 15px;
    }
    .export {
      width: 30%;
      .MuiButton-root {
        min-width: 20px;
        width: 40px;
      }
    }
    .job-selection {
      justify-content: flex-end;
      .MuiButton-root {
        margin-right: 0;
      }
    }
    .al-select {
      width: 38%;
      margin-right: 15px;

      .MuiOutlinedInput-root {
        height: 40px;
        color: #535353;
      }
      .MuiOutlinedInput-notchedOutline {
        max-width: 100%;
        height: 35px;
        color: #535353;
      }
    }
  }
  .candidates-profile {
    .profile-contact {
      img {
        max-width: 150px;
        max-height: 150px;
        border-radius: 5px;
        box-shadow: 2px 3px 7px 2px rgb(0 0 0 / 32%);
      }
    }
    .sub-header {
      font-size: 12px;
      font-weight: 600;
    }
    .profile-name {
      font-size: 14px;
      font-weight: 700;
    }
    .profile-summary {
      line-height: 1.6;
    }
    .MuiChip-root {
      height: 28px;
      border-radius: 5px;
      margin-right: 8px;
      color: #535353;
      background-color: #f3f3f3;
      // font-size: 12px;
    }
  }
}
