@use '../../../../../styling/colors' as *;
.user-toggle {
    height: 150px;
    width: 48%;
    border: 1px solid #305dba;
    border-radius: 10px;
    margin: 1%;
    padding: 0 5px;
    box-shadow: 0 0 6px 5px #f1eeee;
    .display-message {
        width: 75%;
        font-weight: 500;
    }
    .primary-blue{
        color: $primaryBlue;
    }
}