.render-area {
  width: 98%;
  padding: 0 15px;
  margin: 0 0.9% 10px 0.9%;
  @media only screen and (min-width: 1201px) {
    width: 99%;
    margin: 0 0.5% 10px 0.5%;
  }
  .badge-values {
    .MuiChip-root {
      border: 0.5px solid #7bbcd8;
      background-color: #e4f7fe;
      color: #67696a;
      margin: 3px;
    }
    .MuiSvgIcon-root {
      font-size: 14px;
    }
  }
  .split-border {
    border-bottom: 0.5px solid #e2e3e5;
    width: 100%;
    margin: 35px 0;
  }
}
.MuiDrawer-paper {
  background-color: #5c8dd9 !important;
}
.t-bold {
  font-weight: 700;
}
.al-footer {
  position: fixed;
  bottom: 0;
  background-color: #f4f4f4;
  padding: 10px;
  font-size: 12px;
  z-index: 100;
  box-shadow: 0 -1px 4px 0 rgb(0 0 0 / 10%);
}
