@use '../../styling/colors' as *;
@use '../../styling/layout' as *;

.al-filter-bar {
  @extend .d-flex;
  @extend .h-center;
  height: 60px;
  width: 100%;
  margin:10px 0;
  padding: 5px 15px;
  background-color: white;
  box-shadow: 0 1px 4px 0 rgb(0 0 0 / 10%);
  border-radius: 5px;
  .search-bar {
    width: 70%;
    margin-right: 1%;
    .al-input .MuiTextField-root .MuiOutlinedInput-root {
      border-radius: 3px;
      color: #828485;
    }

    .al-input .MuiTextField-root {
      width: 100%;
    }
  }
  .filter-dropdown {
    width: 23%;
    margin-right: 1%;
  }
  .MuiOutlinedInput-notchedOutline {
    border-color: #ededed;
  }
  .MuiInputLabel-root{
    color: #828485;
  }
}
