@use '../../../../styling/layout' as *;
@use '../../../../styling/colors' as *;

.info-banner {
  @extend .d-flex-center;
  @extend .v-center;
  width: 40%;
  background-color: $primaryBlue;
  border-radius: 5px;
  padding: 10px;
  .catch-phrase {
    width: 80%;
    color: $primaryWhite;
    padding: 50px 0;
    .ruler {
      width: 40%;
      border: 1px solid $primaryOrange;
      margin: 20px 0;
    }
    .phrase {
      font-size: 48px;
      font-weight: 500;
      letter-spacing: 1px;
      margin: 2px 0;
      &.secondary {
        color: $primaryOrange;
      }
    }
  }
}
