@use '../../styling/colors' as *;
@use '../../styling/layout' as *;

.card {
  @extend .d-flex;
  cursor: pointer;
  margin: 0.9%;
  @media only screen and (max-width: 48px) {
    width: 100%;
  }
  @media only screen and (min-width: 481px) and (max-width: 780px) {
    width: 48%;
  }
  @media only screen and (min-width: 781px) and (max-width: 1200px) {
    width: 31.5%;
  }
  @media only screen and (min-width: 1201px) {
    width: 24%;
    margin: 0.5%;
  }

  box-shadow: 0 1px 4px 0 #00000033;
  transition: 0.3s;
  padding-top: 10px;
  min-height: 120px;
  border-radius: 5px;
  background-color: white;
  &:hover {
    box-shadow: 0 8px 16px 0 #00000033;
  }
  .card-info-wrapper {
    padding: 0 20px;
    width: 100%;

    .card-info-bar {
      width: 100%;
      margin: 5px 0;
      @extend .d-flex-center;
      .image-holder, .rating-holder {
        width: 60%;
        img {
          height: 30px;
        }
      }
      .card-status {
        @extend .d-flex;
        @extend .h-right;
        width: 40%;
      }
    }
    .card-header {
      width: 100%;
      margin: 5px 0 2px 0;
      font-weight: 700;
    }
    .card-info-text {
      width: 100%;
      margin: 10px 0;
      font-size: 12px;
      color: #696969;
      
    }
  }
  .card-activity {
    @extend .d-flex;
    @extend .h-center;
    width: 100%;
    height: 40px;
    padding: 0 20px;
    border-top: 1px solid #e0e0e0;
    margin-top: 5px;
    font-size: 12px;
    color: #a9a6a6;
  }
}
