@use '../../styling/colors' as *;
@use '../../styling/layout' as *;

.al-summary-bar {
  color: white;
  font-weight: 600;
  .first-elm,
  .second-elm,
  .third-elm,
  .fourth-elm,
  .fifth-elm {
    border: 1px solid white;
    width: 30px;
    height: 30px;
    @extend .d-flex-center;
    font-size: 12px;
  }
  .first-elm {
    background-color: #01266d;

    border-radius: 5px 0 0 5px;
  }
  .second-elm {
    background-color: #1c3c8f;
  }
  .third-elm {
    background-color: #345aab;
  }
  .fourth-elm {
    background-color: #0f6ea8;
  }
  .fifth-elm {
    background-color: #358caa;
    border-radius: 0 5px 5px 0;
  }
}
