.al-sidebar-item {
  width: 280px;
  .logo {
    justify-content: center;
  }
  .navigation-items {
    margin-top: 15px;
    width: 100%;
    a {
      text-decoration: none;
    }
    .navigation-item {
      color: white;
      margin: 10px 0;
      height: 40px;
      padding-left: 40px;
      .display-name {
        margin-left: 10px;
        font-weight: 600;
      }
      &.selected {
        background-color: rgb(255, 255, 255, 0.2);
        border-left: 6px solid #a1c0ef;
        padding-left: 34px
      }
    }
  }
}
