@use '../../styling/colors' as *;
@use '../../styling/layout' as *;

.al-details {
  width: 100%;
  background-color: white;
  margin-top: 10px;
  padding: 20px;
  box-shadow: 0 1px 4px 0 rgb(0 0 0 / 20%);
  .details-header {
    width: 100%;
    .image-holder {
      @extend .d-flex-center;
      width: fit-content;
      padding-right: 20px;
      border-right: 1px solid #c5c4c4;
      img {
        height: 30px;
      }
    }
    .details-info-header {
      font-weight: 700;
    }
    .details-info {
      padding: 10px 15px;
      width: 30%;
      font-size: 14px;
      min-width: 200px;
     
      .other-details-info {
        margin-top: 5px;
        color: #535353;
        font-size: 13px;
      }
    }
    .details-control {
      width: 20%;
      max-width: 110px;
      justify-content: flex-end;
      .al-select {
        width: 100%;
        .MuiOutlinedInput-root {
          height: 30px;
          color: #4578C8;
          font-size: 12px;
          font-weight: 600;
        }
        .MuiOutlinedInput-notchedOutline {
          border-color: #ededed;
          max-width: 110px;
          padding: 5px 10px;
          border-radius: 5px;
          height: 25px;
          
          border: 1px solid #E6E6E6;
          color: #4578C8;
          font-size: 12px;
        }

      }
      .details-toggle{
        margin-top: 20px;
        font-size: 12px;
        font-weight: 600;
        color: #4578C8;
        cursor: pointer;
      }
    }
  }
  .details-tab {
    width: 100%;
    margin-top: 20px;
  }
  .sub-header {
    font-size: 14px;
    font-weight: 600;
    margin-top: 25px;
  }
  .al-input {
    width: 33%;
    margin-top: 20px;
    .form-label {
      margin: 5px 0;
      font-size: 12px;
    }
    .MuiOutlinedInput-root {
      border-radius: 10px;
      height: 45px;
    }
  }
  .action-btn {
    margin-top: 20px;
    .MuiButton-root {
      margin-right: 15px;
    }
  }
  .add-branch,
  .add-invoice-location,
  .add-users {
    margin: 0.5%;
    width: 98%;
  }
  .MuiButton-root {
    min-width: 100px;
  }
}
