.al-slider {
  .MuiSlider-rail {
    color: #e0e0e0;
    height: 2px;
  }
  .MuiSlider-track {
    color: #2c599f;
    height: 1px;
  }
  .MuiSlider-thumb {
    width: 10px;
    height: 10px;
    background-color: white;
    border: 2px solid #2c599f;
  }
  .MuiSlider-valueLabel {
    background-color: white;
    color: #828485;
    border: 1px solid #828485;
    padding: 0.15rem 1rem;
    border-radius: 5px;
  }
}
