.job-details {
  a{ 
    text-decoration: none;
    
  }
  .job-preview {
    font-size: 14px;
    padding: 10px 20px;
    color: #828485;
    line-height: 1.4;
    .split-border {
      margin: 20px 0;

    }
    .sub-header {
      margin-top: 5px;
      color: black;
    }
    li {
      margin: 15px 0;
      font-size: 14px;
      &::marker {
        color: #4578C8;
        font-size: 14px;
      }
    }
    .badge-values{
      margin: 15px 0 ;
    }
    .highlighted{
      border-radius: 8px;
      padding: 20px 15px;
      margin-bottom: 15px;

    }
  }
  .job-list-filters {
    width: 100%;
    margin-top: 15px;
    background-color: white;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 1px 4px 0 rgb(0 0 0 / 20%);
    .job-list-filters-header {
      font-weight: 600;
    }
  }
  .job-list {
    width: 100%;
    margin-top: 10px;
  }
}
