@use '../../styling/colors' as *;
@use '../../styling/layout' as *;

.user-entry {
  @extend .d-flex-center;
  flex-direction: column;
  height: 100vh;
  background: linear-gradient(180deg, $secondaryBlue 50%, $secondaryWhite 50%);
  .user-entry-container {
    @extend .d-flex-center;
    width: 70%;
    // height: 80%;
    .card {
      @extend .w-100;
      @extend .d-flex;
      height: fit-content;
      background-color: $primaryWhite;
      border-radius: 5px;
      padding: 20px;
    }
  }
}
