@use '../../../styling/colors' as *;

.job-form {
  background-color: white;
  box-shadow: 0 1px 4px 0 #00000033;
  margin: 10px 0;
  padding: 15px 20px;
  border-radius: 5px;
  h5 {
    width: 100%;
    margin: 0;
  }
  .mt-20 {
    margin-top: 20px;
  }

  .experience,
  .gross-pay {
    width: 30%;
    margin-top: 20px;
    margin-right: 3%;
  }

  .form-label {
    font-size: 12px;
    color: $secondaryBlack;
    margin-bottom: 10px;
  }
  .job-priority {
    .form-label {
      margin-bottom: 0;
      margin-right: 15px;
    }
  }
  .job-description,
  .note-recruiters {
    width: 100%;
    margin-top: 20px;
    .al-text-area{
      margin-top: 20px;
    }
    textarea {
      border: 1px solid #cfcfcf;
      width: 97%;
      border-radius: 10px;
    }
  }
  .key-skills{
    width: 97%;
    // padding: 15px;
    // border: 0.5px solid $secondaryBlack;
    border-radius: 10px;
    
  }
  .al-input {
    width: 33%;
    margin-top: 20px;
    .form-label {
      margin: 5px 0;
      font-size: 12px;
    }
    .MuiOutlinedInput-root {
      border-radius: 10px;
      height: 45px;
    }
  }
  .form-action{
    .MuiButtonBase-root  {
      margin-right: 10px;
      font-size: 12px;
    }
  }
}
