@use '../../../../styling/colors' as *;

.login {
  width: 60%;
  padding: 20px;
  .login-form {
    padding: 15px 0;
    .form-field {
      .form-label {
        margin: 15px 0;
        color: $secondaryBlack;
      }
      .MuiTextField-root {
        width: 90%;
        .MuiOutlinedInput-root {
          border-radius: 10px;
        }
      }
    }
    .login-action {
      margin: 15px 0;
      font-size: 14px;
      color: $secondaryBlack;
      justify-content: space-between;
      width: 90%;
    }
  }
}
