@use '../../styling/layout';

.al-full-calendar {
  .fc-header-toolbar {
    background-color: white;
    padding: 10px;
    margin-bottom: 0 !important;
    .fc-button {
      background-color: #8ab6fc;
      border: none;
      text-transform: capitalize;
      font-size: 13px;
      font-weight: 600;
    }
    .fc-button-active {
      background-color: #6c9deb !important ;
    }
    .fc-toolbar-chunk {
      @extend .d-flex-center;
      .fc-toolbar-title {
        font-size: 18px;
      }
      .fc-prev-button,
      .fc-next-button {
        background-color: white;
        border-radius: 25px;
        border: 0.5px solid #5c5c5c38;
        .fc-icon {
          color: #565656;
          font-size: 12px;
        }
      }
    }
  }
  .fc-scrollgrid {
    border: none;
    background-color: white;
    padding-top: 10px;
    height: 70vh;
    .fc-col-header {
      .fc-col-header-cell {
        padding: 0 2px;
        .fc-scrollgrid-sync-inner {
          background-color: #4578c8;
          border-radius: 5px;
          padding: 10px 0;
          color: white;
          font-size: 12px;
        }
      }
    }
    th,
    td {
      border: none;
    }

    .fc-day {
      padding: 2px;
      .fc-scrollgrid-sync-inner {
        background-color: #f7f3f3;
        border-radius: 5px;
        padding: 5px 0;
        font-weight: 600;
      }
    }
    .fc-day-today {
      .fc-scrollgrid-sync-inner {
        background-color: #eef3fb;
      }
    }
  }
  .event-info, .view-details {
    margin-top: 10px;
    font-size: 10px;
    white-space: normal;
    color: #4578C8;
    margin-left: 10px;
    cursor: default;
  }
  .fc-daygrid-dot-event:hover {
    background-color: unset;
  }
  .view-details {
    color: #27292D;
    cursor: pointer;
    margin-top: 5px;
  }
}
