@use '../../styling/colors' as *;
@use '../../styling/layout' as *;

.al-breadcrumbs {
  @extend .d-flex;
  @extend .h-center;
  width: 100%;
  padding: 5px 10px;
  background-color: white;
  height: 50px;
  box-shadow: 0 1px 4px 0 rgb(0 0 0 / 10%);
  border-radius: 0 0 5px 5px;
  color: #4578c9;
  font-weight: 700;
  font-size: 16px;
  
  .navigateback {
    @extend .d-flex-center;
    margin: 10px;
    img {
      width: 20px;
      height: 20px;
    }
  }
}
