@use '../../../../styling/layout' as *;
@use '../../../../styling/colors' as *;

.create-account {
  width: 60%;
  @extend .d-flex-center;
  padding: 0 30px;
  .create-account-info {
    @extend .d-flex;
    @extend .v-center;
    font-size: 23px;
    font-weight: 600;
    width: 100%;
    margin: 30px 0;
  }
  .sign-in-toggle {
    margin: 15px 0;
  }
  .primary-blue {
    color: $primaryBlue;
    margin: 0 5px ;
  }
  
}
